import {ThemeProvider} from 'emotion-theming';
import React from 'react';
import {Provider} from 'urql';
import MediumPosts from '@js-ni/medium-posts';
import {Box} from '@nerdify/styled-system-primitives';

import graphqlClient from './graphqlClient';
import theme from './theme';
import Events from './components/Events';
import Footer from './components/Footer';
import Header from './components/Header';
import OrgMembersDisplay from './components/Members';
import RepositoryList from './components/RepositoryList';
import Talks from './components/Talks';
import Welcome from './components/Welcome';

function App() {
  return (
    <Provider value={graphqlClient}>
      <ThemeProvider theme={theme}>
        <Header />
        <Welcome />
        <Box bg="#eff1f5" py={4}>
          {/* Members list */}
          <OrgMembersDisplay orgName="js-ni" />

          <Box mt={4} mx="auto" maxWidth="948px" px={[3, 4, 4, 0]}>
            <MediumPosts />
            <RepositoryList mt={4} />
          </Box>
        </Box>
        <Talks />
        <Events />
        <Footer />
      </ThemeProvider>
    </Provider>
  );
}

export default App;
