import React from 'react';
import {For} from 'react-loops';
import {Grid} from '@nerdify/styled-system-primitives';

/**
 * Element that displays a Github profile card. For now it only displays
 * the member profile picture with a link to member profile.
 *
 * Props required:
 * - member: A dictionay of a Github profile that has at least the avatar and username
 */
export function MemberProfile({member}) {
  return (
    <a href={member.html_url} title={member.login}>
      <img
        alt={member.login}
        height="64px"
        src={`${member.avatar_url}&size=128`}
        style={{}}
        width="64px"
        css={theme => ({borderRadius: theme.radii[3]})}
      />
    </a>
  );
}

/**
 * Displays a list of github profiles. Each profile is displayed in a MemberProfile element
 *
 * Props required:
 * - members: An array of github dictinaries profiles
 */
export function OrganizationMembers({members}) {
  return (
    <Grid
      gridGap={2}
      gridTemplateColumns="repeat(auto-fit, 64px)"
      justifyContent="center"
    >
      <For of={members} as={member => <MemberProfile member={member} />} />
    </Grid>
  );
}
