export default /* GraphQL */ `
  query Repositories($first: Int!) {
    organization(login: "js-ni") {
      id
      repositories(
        first: $first
        orderBy: {field: PUSHED_AT, direction: DESC}
      ) {
        pageInfo {
          hasNextPage
        }
        nodes {
          forkCount
          id
          name
          shortDescriptionHTML
          url
          primaryLanguage {
            color
            name
          }
          stargazers {
            totalCount
          }
        }
      }
    }
  }
`;
