import React from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {Box, Text} from '@nerdify/styled-system-primitives';

function RepositoryItem({
  repository: {
    forkCount,
    name,
    primaryLanguage,
    shortDescriptionHTML,
    stargazers,
    url,
  },
  ...props
}) {
  return (
    <Box
      {...props}
      bg="white"
      display="flex"
      flexDirection="column"
      p={3}
      css={theme => ({
        border: '1px solid #d1d5da',
        borderRadius: theme.radii[2],
        boxShadow: theme.shadows.small,
      })}
    >
      <Text as="a" fontWeight={5} href={url}>
        {name}
      </Text>

      <Box
        as="p"
        color="#586069"
        dangerouslySetInnerHTML={{__html: shortDescriptionHTML}}
        fontSize={0}
        mb={3}
        flex="1 0"
      />

      <Box
        color="#586069"
        display="flex"
        fontSize={0}
        css={theme => ({lineHeight: theme.lineHeights[0]})}
      >
        {primaryLanguage && (
          <Box display="flex" mr={3}>
            <Box
              as="span"
              display="block"
              height={12}
              mr={1}
              width={12}
              css={{borderRadius: '50%'}}
              style={{backgroundColor: primaryLanguage.color}}
            />
            <Text fontSize={0}>{primaryLanguage.name}</Text>
          </Box>
        )}

        {stargazers.totalCount > 0 && (
          <Box mr={3}>
            <FontAwesomeIcon fixedWidth icon="star" />
            {stargazers.totalCount}
          </Box>
        )}

        {forkCount > 0 && (
          <Box>
            <FontAwesomeIcon fixedWidth icon="code-branch" />
            {forkCount}
          </Box>
        )}
      </Box>
    </Box>
  );
}

export default RepositoryItem;
