import {format, parseISO} from 'date-fns';
import esLocale from 'date-fns/locale/es';
import React from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {Box, Heading, Text} from '@nerdify/styled-system-primitives';

function buildStaticMapUrl(lat, lng) {
  return `https://maps.googleapis.com/maps/api/staticmap?center=${lat},${lng}&zoom=16&size=580x300&markers=color:red%7Clabel:%7C12.136062922753,-86.266359686852&key=${
    process.env.REACT_APP_GOOGLE_MAP_KEY
  }`;
}

function EventCard(props) {
  const {place} = props;
  const {location} = place;
  const {latitude, longitude} = location;

  return (
    <Box
      bg="white"
      css={theme => ({borderRadius: theme.radii[2], overflow: 'hidden'})}
    >
      <Box
        as="img"
        alt={props.name}
        height="auto"
        maxWidth="100%"
        src={buildStaticMapUrl(latitude, longitude)}
      />

      <Box p={3}>
        <Heading fontSize={3}>
          <Text
            as="a"
            color="#4183c4"
            href={`https://www.facebook.com/events/${props.id}`}
          >
            {props.name}
          </Text>
        </Heading>

        <Text color="#777" mt={2}>
          <div css={{textTransform: 'capitalize'}}>
            <FontAwesomeIcon fixedWidth icon={['far', 'calendar-alt']} />
            {format(parseISO(props.start_time), 'MMMM d, y', {
              locale: esLocale,
            })}
          </div>
          <div>
            <FontAwesomeIcon fixedWidth icon={['far', 'clock']} />
            {format(parseISO(props.start_time), 'hh:mm a')} -{' '}
            {format(parseISO(props.end_time), 'hh:mm a')}
          </div>
          <div>
            <Text
              as="a"
              color="#777"
              href={`https://www.google.com/maps/@${latitude},${longitude},17z`}
            >
              <FontAwesomeIcon fixedWidth icon={['far', 'map']} />
              {place.name}
            </Text>
          </div>
          <div>
            <FontAwesomeIcon fixedWidth icon={['fas', 'globe-americas']} />
            {location.city}, {location.country}
          </div>
        </Text>
      </Box>
    </Box>
  );
}

export default EventCard;
