import React, {useState} from 'react';
import {For} from 'react-loops';
import {useQuery} from 'urql';
import {Box, Grid, Heading} from '@nerdify/styled-system-primitives';

import query from './query';
import RepositoryItem from '../RepositoryItem';

const REPOSITORIES_TO_FETCH = 6;

function RepositoryList(props) {
  const [count, setCount] = useState(REPOSITORIES_TO_FETCH);
  const [{data, fetching}] = useQuery({
    query,
    variables: {first: count},
  });

  if (!data) {
    return null;
  }

  const {repositories} = data.organization;
  const {hasNextPage} = repositories.pageInfo;

  return (
    <Box {...props}>
      <Heading>Repositorios</Heading>
      <Grid gridGap={3} gridTemplateColumns="repeat(3, 1fr)" mt={3}>
        <For
          of={repositories.nodes}
          as={repository => {
            return <RepositoryItem repository={repository} />;
          }}
        />
      </Grid>

      {hasNextPage && (
        <Box
          as="button"
          bg="white"
          disabled={fetching}
          fontSize={2}
          mt={3}
          onClick={() => setCount(count => count + REPOSITORIES_TO_FETCH)}
          p={3}
          width="100%"
          css={theme => ({
            border: 'none',
            boxShadow: theme.shadows.small,
            borderRadius: theme.radii[2],
          })}
        >
          {fetching ? 'Cargando...' : 'Cargar más'}
        </Box>
      )}
    </Box>
  );
}

export default RepositoryList;
